import React, { Component, useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Header from "../components/Header";
import { serverUrl } from "../utils/api";
import Footer from "../components/Footer";

import { Helmet } from "react-helmet";

const CategoryPage = (props) => {
  const [products, setProducts] = useState([]);

  const [query, setQuery] = useState(props.location.search.split("=")[1]);
  const [cartItems, setCartItems] = useState([]);
  const [currencyIndex, setCurrencyIndex] = useState();
  const [AUD, setAUD] = useState(1);
  const [EUR, setEUR] = useState(1);

  const handleCart = async (product) => {
    await setCartItems([...cartItems, product]);

    toast.success("Product added to cart");
    props.history.push("/cart");
  };

  //Component Did Mount
  useEffect(() => {
    const prevItems = localStorage.getItem("cartItems");

    sessionStorage.setItem("cartItems", prevItems);
    setCartItems(JSON.parse(prevItems));

    setAUD(localStorage.getItem("AUD"));
    setEUR(localStorage.getItem("EUR"));
    setCurrencyIndex(localStorage.getItem("currency"));
  }, []);

  //Unique Items here
  useEffect(() => {
    let cartThings = [];
    if (cartItems) cartThings = getUniqueCartItems();

    sessionStorage.setItem("cartItems", JSON.stringify(cartThings));
    localStorage.setItem("itemCount", cartThings.length);
  }, [cartItems]);

  function onlyUnique(value, index, self) {
    return self.indexOf(value) === index;
  }
  const getUniqueCartItems = () => {
    const unique = cartItems.filter(onlyUnique);
    return unique;
  };
  useEffect(() => {
    if (props.location.search.split("=")[1] == "software-keys") {
      axios
        .all([
          axios.post(`${serverUrl}/api/products/fetch/products/tag`, {
            tag: "pc-tools",
          }),
          axios.post(`${serverUrl}/api/products/fetch/products/tag`, {
            tag: "office-and-windows",
          }),
          axios.post(`${serverUrl}/api/products/fetch/products/tag`, {
            tag: "antivirus",
          }),
        ])
        .then(
          axios.spread((data1, data2, data3) => {
            // output of req.

            setProducts([...data1.data.data, ...data2.data.data, ...data3.data.data]);
            setQuery(props.location.search.split("=")[1]);
          })
        );
    } else {
      axios
        .post(`${serverUrl}/api/products/fetch/products/tag`, {
          tag: props.location.search.split("=")[1],
        })
        .then((result) => {
          setProducts(result.data.data);
          setQuery(props.location.search.split("=")[1]);
        });
    }
  }, []);

  setTimeout(() => {
    setQuery(props.location.search.split("=")[1]);
  }, 1000);

  useEffect(() => {
    axios
      .post(`${serverUrl}/api/products/fetch/products/tag`, {
        tag: props.location.search.split("=")[1],
      })
      .then((result) => {
        setProducts(result.data.data);
      });
  }, [query]);

  return (
    <div>
      <Helmet>
        <title>{"KeyKes - " + query}</title>
        <meta charset="utf-8" />
        <meta name="viewport" content="width=device-width, initial-scale=1, shrink-to-fit=no" />

        <meta name="description" content={query} />
        <meta
          name="keywords"
          content="Windows key, windows 10, windows 11, office 365 , office 2021, office 19, office 2016, office 2019, graphic design bundle, WordPress themes and plugins, marketing tools, WhatsApp marketing tool, Facebook marketing tool, Instagram marketing tool, WordPress themes, elementor pro, Astra pro, premium plugins, premium themes, pro themes for WordPress, source codes for games, source code for ecommerce, eBook store,  online courses "
        />
        <meta name="robots" content="index,follow" />
      </Helmet>
      <Header shouldDisplay={true} />
      <section>
        <ToastContainer />
        <div>
          <div>
            <div>
              <div>
                <h3 style={{ padding: " 2rem", color: "white" }}>Products</h3>
                <div>
                  <div className="cat-page">
                    {products.map((product, index) => {
                      return (
                        <div class="content__card ">
                          <Link to={`/product?id=${product._id}`}>
                            <img src={product.image} />
                          </Link>
                          <h3>{product.productName}</h3>

                          <h6>
                            {currencyIndex == "EUR"
                              ? "EUR" + (product.price.productPrice * EUR).toFixed(2)
                              : currencyIndex == "AUD"
                              ? "AUD" + (product.price.productPrice * AUD).toFixed(2)
                              : "USD" + product.price.productPrice}
                          </h6>
                          <ul>
                            <li>
                              <i class="fa fa-star" aria-hidden="true"></i>
                            </li>
                            <li>
                              <i class="fa fa-star" aria-hidden="true"></i>
                            </li>
                            <li>
                              <i class="fa fa-star" aria-hidden="true"></i>
                            </li>
                            <li>
                              <i class="fa fa-star" aria-hidden="true"></i>
                            </li>
                            <li>
                              <i class="fa fa-star" aria-hidden="true"></i>
                            </li>
                          </ul>
                          <button class="buy-2" onClick={() => handleCart(product)}>
                            Buy Now
                          </button>
                        </div>
                      );
                    })}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <Footer />
    </div>
  );
};

export default CategoryPage;
