import React from "react";
import Slider from "react-slick";

export default function SimpleSlider() {
  var settings = {
    dots: true,
    infinite: true,
    speed: 1000,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
      autoplaySpeed: 3000,
      swipeToSlide: true,
      pauseOnHover: true,
      fade:true,
      arrows: false,
  };
  return (
    <Slider {...settings}>
      <div className="slick-slider">
      <img
            src="./banner-1.webp"
            style={{
              height: "65vh",
              width: "100%",
              visibility: "visible",
              marginTop: "10px",
              objectFit: "contain",
              
            }} alt="keykes"
          />
      </div>
      <div>
      <img
            src="./banner-2.webp"
            style={{
              height: "65vh",
              width: "100%",
              visibility: "visible",
              marginTop: "10px",
              objectFit: "contain",
              
            }} alt="keykes"
          />
      </div>   <div>
      <img
            src="./banner-3.webp"
            style={{
              height: "65vh",
              width: "100%",
              visibility: "visible",
              marginTop: "10px",
              objectFit: "contain",
              
            }}alt="keykes"
          />
      </div>   <div>
      <img
            src="./banner-4.webp"
            style={{
              height: "65vh",
              width: "100%",
              visibility: "visible",
              marginTop: "10px",
              objectFit: "contain",
              
            }}alt="keykes"
          />
      </div>   <div>
      <img
            src="./banner-5.webp"
            style={{
              height: "65vh",
              width: "100%",
              visibility: "visible",
              marginTop: "10px",
              objectFit: "contain",
              
            }}alt="keykes"
          />
      </div>
    </Slider>
  );
}