import React, { useState, useEffect } from "react";
import { serverUrl } from "../utils/api";
import { Link } from "react-router-dom";
import { useHistory } from "react-router-dom";
import axios from "axios";
import Navbar from "../components/Navbar";
import Navbar1 from "../components/Navbar1";

export default function Header({ shouldDisplay }) {
  const history = useHistory();
  const [count, setCount] = useState(0);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [searchItems, setSearchItems] = useState([]);
  const [searchDisplayItems, setSearchDisplayItems] = useState([]);
  const [currency, setCurrency] = useState("USD");
  const [cartNumber,setCartNumber] = useState(
    sessionStorage.getItem("cartItems") ?  JSON.parse(sessionStorage.getItem("cartItems")).length : [].length
  )
  //code for currency change everywhere
  const [aud, setAud] = useState("");
  const [cad, setCad] = useState("");
  //code for currency change everywhere

  const handleSearchQuery = async (query) => {
    var products = [];

    searchItems.forEach((elem) => {
      if (elem.productName.toLowerCase().includes(query.toLowerCase())) products.push(elem);
    });

    setSearchDisplayItems(products);
    if (!query) {
      setSearchDisplayItems([]);
    }
  };

  const handleLogout = () => {
    localStorage.removeItem("token");
    setIsLoggedIn(false);
    history.push("/");
  };

  const handleCurrencyChange = (value) => {
    setCurrency(value);
    localStorage.setItem("currency", value);
    window.location.reload(true);
  };

  useEffect(() => {
    setCount(window.localStorage.getItem("itemCount"));
    if (localStorage.getItem("token")) {
      setIsLoggedIn(true);
    } else {
      setIsLoggedIn(false);
    }

    setCurrency(localStorage.getItem("currency"));
    //Fetching products for searchbar
    const data = { queryType: "searchBarOnly" };
    axios
      .post(`${serverUrl}/api/products/search/products`, data)
      .then((res) => {
        setSearchItems(res.data.data);
      })
      .catch(() => {
        //handle error
      });
  }, []);

  useEffect(() => {
    //code for currency change everywhere

    axios
      .get("https://freecurrencyapi.net/api/v2/latest?apikey=c0ab0bf0-4c2c-11ec-88b2-a34f313ca7a8")
      .then((result) => {
        localStorage.setItem("AUD", result.data.data.AUD);
        localStorage.setItem("EUR", result.data.data.EUR);
        setCurrency(localStorage.getItem("currency"));
      })
      .catch((err) => {
        //handle error
        console.log(err);
      });
  }, []);

  const loginCondition = isLoggedIn ? (
    <>
      <li>
        <Link to="/dashboard">
          <a href data-toggle="modal">
            Dashboard
          </a>
        </Link>
      </li>
      <li onClick={handleLogout}>
        <a href data-toggle="modal">
          Logout
        </a>
      </li>
    </>
  ) : (
    <li>
      <Link to="/account">
        <a href data-toggle="modal" data-target="#login-modal">
          Login
        </a>
      </Link>
    </li>
  );
  const searchBarProducts = searchDisplayItems.length
    ? searchDisplayItems.map((item, index) => (
        <Link to={`/product?id=${item._id}`}>
          <li>
            <img src={item.image} /> <span>{item.productName}</span>
          </li>
        </Link>
      ))
    : null;

  

    

  return (
    <div>
      <>
        {/* Start header section */}
        <header id="aa-header">
          {/* start header top  */}
          <div className="aa-header-top">
            <div className="container">
              <div className="row">
                <div className="col-md-12">
                  <div className="aa-header-top-area">
                    {/* start header top left */}
                    <div className="aa-header-top-left">
                      {/* start language */}
                      {shouldDisplay ? (
                        <div className="aa-language">
                          <select
                            onChange={(e) => handleCurrencyChange(e.target.value)}
                            value={currency}
                          >
                            <option value="USD">USD</option>
                            <option value="AUD">AUD</option>
                            <option value="EUR">EUR</option>
                          </select>
                        </div>
                      ) : null}
                      {/* <div className="aa-language">
                        <select
                          onChange={(e) => handleCurrencyChange(e.target.value)}
                          value={currency}
                        >
                          <option value="USD">USD</option>
                          <option value="AUD">AUD</option>
                          <option value="EUR">EUR</option>
                        </select>
                      </div> */}
                      {/* / language */}
                      {/* start currency */}

                      {/* / currency */}
                      {/* start cellphone */}
                      <div className="cellphone hidden-xs">
                        <p>
                          <span className="<fa fa-envelope" />
                          keykesltd@gmail.com
                        </p>
                      </div>
                      {/* / cellphone */}
                    </div>
                    {/* / header top left */}
                    <div className="aa-header-top-right">
                      <ul className="aa-head-top-nav-right">
                        {/* <li>
                          <Link href="/account">
                            <a>My Account</a>
                          </Link>
                        </li> */}

                        <li className="hidden-xs">
                          <Link to="/cart">My Cart</Link>
                        </li>
                        <li className="hidden-xs">
                          <Link to="/checkout"> Checkout</Link>
                        </li>

                        {loginCondition}
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          {/* / header top  */}
          {/* start header bottom  */}
          <div className="aa-header-bottom">
            <div className="container">
              <div className="row">
                <div className="col-md-12">
                  <div className="aa-header-bottom-area">
                    {/* logo  */}
                    <div className="aa-logo">
                      {/* Text based logo */}

                      <a href="https://keykes.com/">
                        <img src=".\keykes-logo.png" alt="logo" height="80" width="80"></img>
                      </a>

                      {/* img based logo */}
                      {/* <a href="index.html"><img src="img/logo.jpg" alt="logo img"></a> */}
                    </div>
                    {/* / logo  */}
                    {/* cart box */}
                    
                    {/* / cart box */}
                    {/* search box */}
                    <div className="aa-search-box">
                      <input
                        type="text"
                        name
                        id
                        onChange={(e) => handleSearchQuery(e.target.value)}
                        placeholder="Search here ex. 'Windows 10..' "
                      />

                      <ul className="search__items" onClick={() => setSearchDisplayItems([])}>
                        {searchBarProducts}
                      </ul>

                      <button>
                        <span className="fa fa-search" />
                      </button>
                    </div>

                    <div className="aa-cartbox">
                      <Link to="/cart">
                        <a className="aa-cart-link">
                          <span  className="fa fa-shopping-cart fa-2x" />
                          <span className="badge"> {cartNumber} </span>
                         
                        
                        </a>
                      </Link>
                    </div>

                    {/* / search box */}
                  </div>
                </div>
              </div>
            </div>
          </div>
          {/* / header bottom  */}
        </header>
        {/* / header section */}
        {/* menu */}

        {/* <section
id="menu"
style={{
  position: "sticky",
  top: "0",
  zIndex: "999",
}}
>
        <Navbar  expand="lg">
  <Container>
   <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="me-auto   " style={{
        letterSpacing:"1px",
        fontSize:"15px",
      
        
}} >
   
        <Nav.Link href="/" >Home</Nav.Link>
     
        <NavDropdown title="Category" id="basic-nav-dropdown">
          <NavDropdown.Item href="">ms-office</NavDropdown.Item>
          <NavDropdown.Item href="/category?tag=ms-office">windows</NavDropdown.Item>
          <NavDropdown.Item href="/category?tag=amazon">amazon</NavDropdown.Item>
          <NavDropdown.Divider />
          <NavDropdown.Item href="/category?tag=netflix">Netflix</NavDropdown.Item>
        </NavDropdown>
        <Nav.Link href="#link">Link</Nav.Link>
      </Nav>
    </Navbar.Collapse>
  </Container>
</Navbar>
</section> */}

        <Navbar />
        <Navbar1 />
      </>
    </div>
  );
}
