import axios from "axios";
import React, { useState } from "react";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Header from "../components/Header";
import { useHistory } from "react-router-dom";
import Footer from "../components/Footer";
import { serverUrl } from "../utils/api";
import { Helmet } from "react-helmet";

export default function Account(props) {
  const history = useHistory();
  const [emailL, setEmailL] = useState("");
  const [passwordL, setPasswordL] = useState("");

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    axios
      .post(`${serverUrl}/api/users/login`, {
        email: emailL,
        password: passwordL,
      })
      .then((result) => {
        // console.log(result);
        if (!result.data.isAuth) {
          toast.error(result.data.msg);
        }

        if (result.data.isAuth) {
          // console.log(result.data);
          localStorage.setItem("token", result.data.token);
          localStorage.setItem("email", result.data.email);
          toast.success("Login Success !");
          props.history.push("/dashboard");
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const handleRegister = (e) => {
    e.preventDefault();
    axios
      .post(`${serverUrl}/api/users/register`, {
        name: name,
        email: email,
        password: password,
      })
      .then((result) => {
        if (!result.data.success) {
          toast.error(result.data.error);
        }

        if (result.data.success) {
          toast.success("Register Successfull ! Please Login to Continue ");
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };
  return (
    <>
      <Helmet>
        <title>Login/Resister</title>
        <meta charset="utf-8" />
        <meta name="viewport" content="width=device-width, initial-scale=1, shrink-to-fit=no" />

        <meta
          name="description"
          content="Buy Windows keys, Anti-virus keys , MS office, graphic bundle, WordPress themes and plugins, marketing tools, source codes, courses, eBooks and many more from Keykes.com – Instant delivery via email, worldwide payment, Easy and hassle-free  digital store. "
        />
        <meta
          name="keywords"
          content="Windows key, windows 10, windows 11, office 365 , office 2021, office 19, office 2016, office 2019, graphic design bundle, WordPress themes and plugins, marketing tools, WhatsApp marketing tool, Facebook marketing tool, Instagram marketing tool, WordPress themes, elementor pro, Astra pro, premium plugins, premium themes, pro themes for WordPress, source codes for games, source code for ecommerce, eBook store,  online courses "
        />
        <meta name="robots" content="index,follow" />
      </Helmet>
      <Header shouldDisplay={false} />
      <div>
        {/* catg header banner section */}
       
        {/* / catg header banner section */}
        {/* Cart view section */}
        <section id="aa-myaccount">
          <div className="container">
            <div className="row">
              <div className="col-md-12">
                <div className="aa-myaccount-area">
                  <div className="row">
                    <div className="col-md-6">
                      <div className="aa-myaccount-login">
                        <h4>Login</h4>
                        <form onSubmit={(e) => handleSubmit(e)} className="aa-login-form">
                          <label htmlFor>
                            Email address<span>*</span>
                          </label>
                          <input
                            type="text"
                            required={true}
                            placeholder=" email"
                            onChange={(e) => setEmailL(e.target.value)}
                          />
                          <label htmlFor>
                            Password<span>*</span>
                          </label>
                          <input
                            type="password"
                            required={true}
                            placeholder="Password"
                            onChange={(e) => setPasswordL(e.target.value)}
                          />
                          <button type="submit" className="aa-browse-btn">
                            Login
                          </button>
                          <label className="rememberme" htmlFor="rememberme">
                            <input type="checkbox" id="rememberme" /> Remember me{" "}
                          </label>
                         
                        </form>
                      </div>
                    </div>
                    <div className="col-md-6">
                      <div className="aa-myaccount-register">
                        <h4>Register</h4>
                        <form onSubmit={(e) => handleRegister(e)} className="aa-login-form">
                          <label htmlFor>
                            Full Name <span>*</span>
                          </label>
                          <input
                            type="text"
                            placeholder="Name "
                            onChange={(e) => setName(e.target.value)}
                            required={true}
                          />
                          <label htmlFor>
                            Email address<span>*</span>
                          </label>
                          <input
                            type="text"
                            placeholder="email"
                            onChange={(e) => setEmail(e.target.value)}
                            required={true}
                          />
                          <label htmlFor>
                            Password<span>*</span>
                          </label>
                          <input
                            type="password"
                            placeholder="Password"
                            required={true}
                            onChange={(e) => setPassword(e.target.value)}
                          />
                          <button type="submit" className="aa-browse-btn">
                            Register
                          </button>
                        </form>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
        {/* / Cart view section */}
      </div>
      <Footer />
      <ToastContainer />
      {/* Same as */}
      <ToastContainer />
    </>
  );
}
