import { useEffect, useState } from "react";
import { serverUrl } from "../utils/api";
import parse from "html-react-parser";
import { Link } from "react-router-dom";
import axios from "axios";
import { useLocation } from "react-router-dom";

import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Header from "../components/Header";
import Footer from "../components/Footer";
import { Helmet } from "react-helmet";

export default function SingleProduct(props) {
  
  
  const query = new URLSearchParams(useLocation().search);
  const id = query.get("id");
 
  var prevItems = !localStorage.getItem("cartItems")
    ? sessionStorage.getItem("cartItems")
    : localStorage.getItem("cartItems");
  if (!prevItems) {
    localStorage.setItem("cartItems", "[]");
    sessionStorage.setItem("cartItems", "[]");
  }
  const [product, setProduct] = useState("");
  const [productId, setProductId] = useState(id);
  const [price, setPrice] = useState("");
  const [currencyIndex, setCurrencyIndex] = useState();
  const [AUD, setAUD] = useState(1);
  const [EUR, setEUR] = useState(1);
  const [username, setUsername] = useState("");
  const [comment, setComment] = useState("");

  // console.log(product.reviews, username, comment);
  //cart handler
  const [cartItems, setCartItems] = useState([]);

  const postReview = (e) => {
    e.preventDefault();
    if (!username && !comment) alert("please provide details");
    else {
      axios
        .post(`${serverUrl}/api/products/review/add/${productId}`, {
          name: username,
          comment: comment,
        })
        .then((data) => {
          toast.info("Review Added !");
        });
    }
  };
  const handleCart = async (product) => {
    await setCartItems([...cartItems, product]);

    toast.success("Product added to cart");
    props.history.push("/cart");
  };

  //Component Did Mount
  useEffect(() => {
    prevItems = !localStorage.getItem("cartItems")
      ? sessionStorage.getItem("cartItems")
      : localStorage.getItem("cartItems");

    setCartItems(JSON.parse(prevItems));
    setAUD(localStorage.getItem("AUD"));
    setEUR(localStorage.getItem("EUR"));
    setCurrencyIndex(localStorage.getItem("currency"));
  }, []);

  //Unique Items here
  useEffect(() => {
    let cartThings = [];
    if (cartItems) cartThings = getUniqueCartItems();

    sessionStorage.setItem("cartItems", JSON.stringify(cartThings));
  }, [cartItems]);

  function onlyUnique(value, index, self) {
    return self.indexOf(value) === index;
  }
  const getUniqueCartItems = () => {
    const unique = cartItems.filter(onlyUnique);
    return unique;
  };
  //cart handler

  useEffect(() => {
    axios
      .get(`${serverUrl}/api/products/product/${id}`)
      .then((result) => {
        setProduct(result.data.data);
        setPrice(result.data.data.price.productPrice);
        setProductId(id);
      })
      .catch((err) => {
        //handle error here
      });
  }, [id]);

  const userReviews = product.reviews
    ? product.reviews.map((review, i) => {
        return (
          <div>
            <h4 className="media-heading">
              <strong style={{ color: "orange" }}>{review.name}</strong> :
            </h4>

            <p style={{ color: "white" }}>{review.comment}</p>
          </div>
        );
      })
    : null;

  return (
    <div>
      <Helmet>
        <title>{product.productName}</title>
        <meta charset="utf-8" />
        <meta name="viewport" content="width=device-width, initial-scale=1, shrink-to-fit=no" />

        <meta name="description" content={product.description} />
        <meta
          name="keywords"
          content="Windows key, windows 10, windows 11, office 365 , office 2021, office 19, office 2016, office 2019, graphic design bundle, WordPress themes and plugins, marketing tools, WhatsApp marketing tool, Facebook marketing tool, Instagram marketing tool, WordPress themes, elementor pro, Astra pro, premium plugins, premium themes, pro themes for WordPress, source codes for games, source code for ecommerce, eBook store,  online courses,"
        />
        <meta name="robots" content="index,follow" />
      </Helmet>
      <div>
        <ToastContainer />
        <Header shouldDisplay={false} />
        <section id="aa-product-details">
          <div className="container">
            <div className="row">
              <div className="col-md-12">
                <div className="aa-product-details-area">
                  <div className="aa-product-details-content">
                    <div className="row">
                      <div className="col-md-5 col-sm-5 col-xs-12">
                        <div className="aa-product-view-slider">
                          <div id="demo-1" className="simpleLens-gallery-container">
                            <div className="simpleLens-container">
                              <div className="simpleLens-big-image-container">
                                <a
                                  data-lens-image="img/view-slider/large/polo-shirt-1.png"
                                  className="simpleLens-lens-image"
                                >
                                  <img
                                    src={product.image}
                                    style={{width:"30rem" }}
                                    className="simpleLens-big-image"
                                  />
                                </a>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                      {/* Modal view content */}
                      <div className="col-md-7 col-sm-7 col-xs-12">
                        <div className="aa-product-view-content">
                          <h3>{product.productName} </h3>

                          <div className="aa-price-block">
                            <span><h4 style={{color:"orange"}}>
                              {currencyIndex == "EUR"
                                ? "EUR " + (price * EUR).toFixed(2)
                                : currencyIndex == "AUD"
                                ? "AUD " + (price * AUD).toFixed(2)
                                : "USD " + price}
                           </h4> </span>
                          </div>
                          {parse(`<span>${product.description}</span>`)}

                          <div className="aa-prod-quantity">
                            <p className="aa-prod-category">
                              Category: <b>{product.tag} </b>
                            </p>
                          </div>
                          <div className="aa-prod-view-bottom">
                            <a
                              className="aa-add-to-cart-btn  buy-2"
                              onClick={() => handleCart(product)}
                              style={{ cursor: "pointer" }}
                            >
                              Buy Now
                            </a>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="aa-product-details-bottom">
                    <ul className="nav nav-tabs" id="myTab2">
                      <li>
                        <a href="#description" data-toggle="tab">
                          Description
                        </a>
                      </li>
                      <li>
                        <a href="#review" data-toggle="tab">
                          Reviews
                        </a>
                      </li>
                    </ul>

                    <div className="tab-content">
                      <div className="tab-pane fade in active" id="description">
                        {parse(`<span>${product.longDescription}</span>`)}
                      </div>

                      <div className="tab-pane fade " id="review">
                        <div className="aa-product-review-area">
                          <h4> Reviews for {product.productName}</h4>
                          <ul className="aa-review-nav">
                            <li>
                              <div className="media">
                                <div
                                  className="media-body"
                                  style={{ overflow: "auto", height: "300px" }}
                                >
                                  {userReviews}
                                </div>
                              </div>
                            </li>
                          </ul>
                          <h4>Add a review</h4>
                          {/* <div className="aa-your-rating">
                            <p>Your Rating</p>
                            <a href="#">
                              <span className="fa fa-star-o" />
                            </a>
                            <a href="#">
                              <span className="fa fa-star-o" />
                            </a>
                            <a href="#">
                              <span className="fa fa-star-o" />
                            </a>
                            <a href="#">
                              <span className="fa fa-star-o" />
                            </a>
                            <a href="#">
                              <span className="fa fa-star-o" />
                            </a>
                          </div> */}

                          <div className="form-group">
                            <label htmlFor="message">Your Review</label>
                            <textarea
                              className="form-control"
                              onChange={(e) => setComment(e.target.value)}
                              rows={3}
                              id="message"
                              defaultValue={""}
                            />
                          </div>

                          <div className="form-group">
                            <label htmlFor="email">Email</label>
                            <input
                              type="email"
                              className="form-control"
                              onChange={(e) => setUsername(e.target.value)}
                              id="email"
                              placeholder="example@gmail.com"
                            />
                          </div>
                          <button onClick={(e) => postReview(e)} className="btn btn-success btn-md">
                            Post a review
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
      <Footer />
    </div>
  );
}
