import React, { useEffect, useState } from "react";
import Header from "../components/Header";
import { serverUrl } from "../utils/api";
import { Helmet } from "react-helmet";
import Footer from "../components/Footer";



export default function Checkout() {
  const [cartItems, setCartItems] = useState([]);
  const [total, setTotal] = useState(0);
  const [ids, setIds] = useState("");
  const [currencyIndex, setCurrencyIndex] = useState();
  const [AUD, setAUD] = useState(1);
  const [EUR, setEUR] = useState(1);
  const [checkoutOption, setCheckoutOption] = useState("PayPal");

  const calculateTotal = (items) => {
    var totalValue = 0;
    items.forEach((item) => {
      totalValue = totalValue + parseFloat(item.price.productPrice);
    });
    setTotal(totalValue.toFixed(2));
  };

  useEffect(() => {
    if (!sessionStorage.getItem("cartItems")) {
      sessionStorage.setItem("cartItems", "[]");
    }
    setCartItems(JSON.parse(sessionStorage.getItem("cartItems")));
    calculateTotal(JSON.parse(sessionStorage.getItem("cartItems")));
    convertProductIds(JSON.parse(sessionStorage.getItem("cartItems")));
    setAUD(localStorage.getItem("AUD"));
    setEUR(localStorage.getItem("EUR"));
    setCurrencyIndex(localStorage.getItem("currency"));
  }, []);

  const convertProductIds = (items) => {
    var idString = "";
    items.forEach((item) => {
      idString = item._id + "-" + idString;
    });

    setIds(idString);
  };

  const handlePayment = () => {
    return checkoutOption == "PayPal"
      ? `${serverUrl}/api/stripe/payment/pay`
      : `${serverUrl}/api/stripe/payment/pay`;
  };

  return (
    <div>
      <Helmet>
        <title>Checkout</title>
        <meta charset="utf-8" />
        <meta name="viewport" content="width=device-width, initial-scale=1, shrink-to-fit=no" />

        <meta
          name="description"
          content="Buy Windows keys, Anti-virus keys , MS office, graphic bundle, WordPress themes and plugins, marketing tools, source codes, courses, eBooks and many more from Keykes.com – Instant delivery via email, worldwide payment, Easy and hassle-free  digital store. "
        />
        <meta
          name="keywords"
          content="Windows key, windows 10, windows 11, office 365 , office 2021, office 19, office 2016, office 2019, graphic design bundle, WordPress themes and plugins, marketing tools, WhatsApp marketing tool, Facebook marketing tool, Instagram marketing tool, WordPress themes, elementor pro, Astra pro, premium plugins, premium themes, pro themes for WordPress, source codes for games, source code for ecommerce, eBook store,  online courses "
        />
        <meta name="robots" content="index,follow" />
      </Helmet>
      <Header shouldDisplay={true} />
      <section id="checkout">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <div className="checkout-area">
                <form method="POST" action={handlePayment()} target="_blank">
                  <div className="row">
                    <div className="col-md-8">
                      <div className="checkout-left">
                        <div className="panel-group" id="accordion">
                          <h4 style={{ color: "green" }}>Order Summary</h4>
                          <div className="aa-order-summary-area">
                            <table className="table table-responsive">
                              <thead>
                                <tr>
                                  <th>Product</th>
                                  <th>Total</th>
                                </tr>
                              </thead>
                              <tbody>
                                {cartItems
                                  ? cartItems.map((item, i) => {
                                      return (
                                        <>
                                          <tr>
                                            <td>{item.productName}</td>
                                            <td>
                                              {currencyIndex == "EUR"
                                                ? "EUR " +
                                                  (item.price.productPrice * EUR).toFixed(2)
                                                : currencyIndex == "AUD"
                                                ? "AUD " +
                                                  (item.price.productPrice * AUD).toFixed(2)
                                                : "USD " + item.price.productPrice}
                                            </td>
                                          </tr>
                                        </>
                                      );
                                    })
                                  : null}
                              </tbody>
                              <tfoot>
                                <tr>
                                  <th>Tax</th>
                                  <td>$0</td>
                                </tr>
                                <tr>
                                  <th>Total</th>
                                  <td>
                                    {" "}
                                    {currencyIndex == "EUR"
                                      ? "EUR " + parseFloat(total * EUR).toFixed(2)
                                      : currencyIndex == "AUD"
                                      ? "AUD " + parseFloat(total * AUD).toFixed(2)
                                      : "USD " + parseFloat(total)}
                                  </td>
                                </tr>
                              </tfoot>
                            </table>

                            <h3>Have a Coupon?</h3>
                            <input
                              className="form-control"
                              placeholder="Enter Coupon Code"
                              name="discount_voucher"
                            ></input>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-4">
                      <div className="checkout-right">
                        <h4>Enter your email where you wanted to receive your product license</h4>
                        <input
                          type="text"
                          name="product_ids"
                          value={ids}
                          style={{ display: "none" }}
                        />

                        <input
                          type="email"
                          placeholder="Enter Valid Email Address"
                          className="form-control w-80"
                          name="email"
                          required={true}
                        ></input>
                        <br />

                        <h4>Payment Method</h4>
                        <div className="aa-payment-method">
                          {/* <label htmlFor="paypal">
                            <input
                              type="radio"
                              id="paypal"
                              name="optionsRadios"
                              defaultChecked
                              onChange={() => setCheckoutOption("PayPal")}
                            />{" "}
                            <b style={{color:"black"}}>  Paypal </b>
                          </label>
                          <img
                            src="https://www.paypalobjects.com/webstatic/mktg/logo/AM_mc_vs_dc_ae.jpg"
                            border={0}
                            alt="PayPal Acceptance Mark"
                          />
                          <br/>
                          <br/> */}
                          <label htmlFor="stripe">
                            <input
                              type="radio"
                              id="paypal"
                              name="optionsRadios"
                              onChange={() => setCheckoutOption("Stripe")}
                            />
                            <b style={{ color: "black" }}> Stripe </b>
                          </label>
                          <img
                            src="https://wine-and-spirits.dk/pub/media/wysiwyg/Stripe-Credit-Cards.png"
                            border={0}
                            alt="PayPal Acceptance Mark"
                          />
                          <br />
                          <br />

                          <button
                            type="submit"
                            className="aa-browse-btn"
                            disabled={total == 0 ? true : false}
                          >
                            Place Order
                          </button>
                          <p>If total price is zero then place Order button won't work</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </section>
      <Footer />
    </div>
  );
}
