import React from "react";


export default function SlickSlider() {
  return (
    <div>
      <div class="mobile-banner">
      <img
            src="./mobile-banner.webp"
            style={{
              height: "65vh",
              width: "100%",
              visibility: "visible",
              marginTop: "10px",
              objectFit: "contain",
              
            }} alt="keykes"
          />
      </div>
     
    </div>
  );
}
