import axios from "axios";
import React, { useEffect, useState } from "react";
import { Helmet } from "react-helmet";
import FeaturedProductSection from "../components/FeaturedProductSection";
import Footer from "../components/Footer";
import Header from "../components/Header";
import SlickSlider from "../components/SlickSlider";
import { useHistory } from "react-router-dom";
import ProductSection from "../components/ProductSection";
import RecentProductSection from "../components/RecentProductSection";
import { ToastContainer, toast } from "react-toastify";
import { serverUrl } from "../utils/api";
import Categories from "../components/Categories";
import SlickSlider1 from "../components/SlickSlider1";

export default function HomePage() {
  const history = useHistory();

  const [currency, setCurrency] = useState();
  const [aud, setAud] = useState("");
  const [cad, setCad] = useState("");
  const [cartItems, setCartItems] = useState([]);
  const [products, setProducts] = useState([]);
  const [fproducts, setFroducts] = useState([]);
  const [newProducts, setNewProducts] = useState([]);

  var prevItems = localStorage.getItem("cartItems");

  if (!prevItems) localStorage.setItem("cartItems", "[]");

  const handleCart = async (product) => {
    await setCartItems([...cartItems, product]);
    toast.info("Product added to cart ! ");
  };

  //Component Did Mount
  useEffect(() => {
    prevItems = localStorage.getItem("cartItems");

    if (!prevItems.length) localStorage.setItem("cartItems", "[]");
    setCartItems(JSON.parse(prevItems));
  }, []);

  //Unique Items here
  useEffect(() => {
    let cartThings = [];
    if (cartItems) cartThings = getUniqueCartItems();

    sessionStorage.setItem("cartItems", JSON.stringify(cartThings));
  }, [cartItems]);

  function onlyUnique(value, index, self) {
    return self.indexOf(value) === index;
  }
  const getUniqueCartItems = () => {
    const unique = cartItems.filter(onlyUnique);
    return unique;
  };

  useEffect(() => {
    axios
      .get("https://freecurrencyapi.net/api/v2/latest?apikey=c0ab0bf0-4c2c-11ec-88b2-a34f313ca7a8")
      .then((result) => {
        localStorage.setItem("AUD", result.data.data.AUD);
        localStorage.setItem("EUR", result.data.data.EUR);
        setCurrency(localStorage.getItem("currency"));
      })
      .catch((err) => {
        //handle error
        console.log(err);
      });

    //pachi add gareko
  }, []);

  useEffect(() => {
    history.push("/");
  }, [currency]);

  useEffect(() => {
    axios.get(`${serverUrl}/api/products/fetch/all`).then((result) => {
      const promotedProducts = result.data.data.filter((product) => product.isPromoted == true);
      setFroducts(promotedProducts);
      setProducts(result.data.data);

      setNewProducts([...result.data.data].reverse());
    });
  }, []);
  return (
    <div>
      <Helmet>
        <title>Keykes - Coupon Store</title>
        <meta charset="utf-8" />
        <meta name="viewport" content="width=device-width, initial-scale=1, shrink-to-fit=no" />

        <meta
          name="description"
          content="Buy Windows keys, Anti-virus keys , MS office, graphic bundle, WordPress themes and plugins, marketing tools, source codes, courses, eBooks and many more from Keykes.com – Instant delivery via email, worldwide payment, Easy and hassle-free  digital store. "
        />
        <meta
          name="keywords"
          content="Windows key, windows 10, windows 11, office 365 , office 2021, office 19, office 2016, office 2019, graphic design bundle, WordPress themes and plugins, marketing tools, WhatsApp marketing tool, Facebook marketing tool, Instagram marketing tool, WordPress themes, elementor pro, Astra pro, premium plugins, premium themes, pro themes for WordPress, source codes for games, source code for ecommerce, eBook store,  online courses "
        />
        <meta name="robots" content="index,follow" />
      </Helmet>
      <Header shouldDisplay={true} />

      <SlickSlider1 />

      <SlickSlider />
      {/* TrustBox widget - Micro Review Count */}
      <div
        className="trustpilot-widget"
        data-locale="en-US"
        data-template-id="5419b6a8b0d04a076446a9ad"
        data-businessunit-id="619b9263221fc2091eb94c7a"
        data-style-height="70px"
        data-style-width="100%"
        data-theme="dark"
        data-stars="1,2,3,4,5"
        data-no-reviews="hide"
        data-scroll-to-list="true"
        data-allow-robots="true"
        data-min-review-count={10}
      >
        <a href="https://www.trustpilot.com/review/keykes.com" target="_blank" rel="noopener">
          Trustpilot
        </a>
      </div>
      {/* End TrustBox widget */}
      <Categories />
      <FeaturedProductSection productsItems={fproducts} cartfunc={handleCart} />
      <ProductSection productsItems={products} cartfunc={handleCart} />
      <RecentProductSection productsItems={newProducts} cartfunc={handleCart} />

      <Footer />
    </div>
  );
}
