import { Link } from "react-router-dom";

import React, { useEffect, useState } from "react";
import Header from "../components/Header";
import { Helmet } from "react-helmet";
import Footer from "../components/Footer";

export default function Cart() {
  const [cartItems, setCartItems] = useState([]);
  const [total, setTotal] = useState(0);
  const [currencyIndex, setCurrencyIndex] = useState();
  const [AUD, setAUD] = useState(1);
  const [EUR, setEUR] = useState(1);

  useEffect(() => {
    setCartItems(JSON.parse(sessionStorage.getItem("cartItems")));
    localStorage.setItem("cartItems", sessionStorage.getItem("cartItems"));

    setAUD(localStorage.getItem("AUD"));
    setEUR(localStorage.getItem("EUR"));
    setCurrencyIndex(localStorage.getItem("currency"));
  }, []);

  useEffect(() => {
    calculateTotal(cartItems);
  }, [cartItems]);

  //remove cart item
  const removeCartItem = (i) => {
    var newCartItems = JSON.parse(sessionStorage.getItem("cartItems"));

    newCartItems.splice(i, 1);

    sessionStorage.setItem("cartItems", JSON.stringify(newCartItems));
    localStorage.setItem("cartItems", JSON.stringify(newCartItems));
    setCartItems(newCartItems);
  };

  const calculateTotal = (items) => {
    var totalValue = 0;
    items.forEach((item) => {
      totalValue = totalValue + parseFloat(item.price.productPrice);
    });
    setTotal(totalValue.toFixed(2));
  };

  return (
    <div>
      <>
        <Helmet>
          <title>Cart</title>
        </Helmet>
        <Header shouldDisplay={true} />
        {/* catg header banner section */}

        {/* / catg header banner section */}
        {/* Cart view section */}
        <section id="cart-view">
          <div className="container">
            <div className="row">
              <div className="col-md-12">
                <div className="cart-view-area">
                  <div className="cart-view-table">
                    <form action>
                      <div className="table-responsive">
                        <table className="table tab1le">
                          <thead>
                            <tr>
                              <th />
                              <th />
                              <th>Product</th>
                              <th>Price</th>
                              {/* <th>Quantity</th> */}
                              <th>Total</th>
                            </tr>
                          </thead>
                          <tbody>
                            {cartItems.map((item, i) => {
                              return (
                                <>
                                  <tr>
                                    <td>
                                      <a
                                        className="remove"
                                        onClick={() => {
                                          removeCartItem(i);
                                        }}
                                      >
                                        <fa className="fa fa-close" />
                                      </a>
                                    </td>
                                    <td>
                                      <a href="#">
                                        <img src={item.image} alt="img" />
                                      </a>
                                    </td>
                                    <td>
                                      <a
                                        className="aa-cart-title"
                                        href="#"
                                        style={{ fontWeight: "1000", fontSize: "20px!important" }}
                                      >
                                        {item.productName}
                                      </a>
                                    </td>
                                    <td>
                                      {currencyIndex == "EUR"
                                        ? "EUR " + (item.price.productPrice * EUR).toFixed(2)
                                        : currencyIndex == "AUD"
                                        ? "AUD " + (item.price.productPrice * AUD).toFixed(2)
                                        : "USD " + item.price.productPrice}
                                    </td>
                                    {/* <td>
                                      <input
                                        className="aa-cart-quantity"
                                        type="number"
                                        defaultValue={1}
                                      />
                                    </td> */}
                                    <td>
                                      {currencyIndex == "EUR"
                                        ? "EUR " + (item.price.productPrice * EUR).toFixed(2)
                                        : currencyIndex == "AUD"
                                        ? "AUD " + (item.price.productPrice * AUD).toFixed(2)
                                        : "USD " + item.price.productPrice}
                                    </td>
                                  </tr>
                                </>
                              );
                            })}
                          </tbody>
                        </table>
                      </div>
                    </form>
                    {/* Cart Total view */}
                    <div className="cart-view-total">
                      <h4>Cart Totals</h4>
                      <table className="aa-totals-table">
                        <tbody>
                          <tr>
                            <th>Vat</th>
                            <td>$0</td>
                          </tr>
                          <tr>
                            <th>Total</th>
                            <td>
                              {currencyIndex == "EUR"
                                ? "EUR " + parseFloat(total * EUR).toFixed(2)
                                : currencyIndex == "AUD"
                                ? "AUD " + parseFloat(total * AUD).toFixed(2)
                                : "USD " +  parseFloat(total).toFixed(2)}
                            </td>
                          </tr>
                        </tbody>
                      </table>
                      <Link to="/checkout">
                        <a className="aa-cart-view-btn">Proceed to Checkout</a>
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
        {/* / Cart view section */}
      </>
      <Footer />
    </div>
  );
}
